import React, { useState, useEffect } from 'react';
import Typography from '@material-ui/core/Typography';
import TodoForm from './TodoForm';
import Paper from '@material-ui/core/Paper';
import TodoList from './TodoList';
import Appbar from '@material-ui/core/Appbar';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';
import UseTodoState from './hooks/UseTodoState';



function TodoApp(){
    const initialTodos = [{id: 1, task: 'Pet a Monkey', completed: false, priority: 0}]

    const { todos, addTodo, removeTodo, toggleTodo, editTodo, changePriority} = UseTodoState(initialTodos)

    useEffect(() => {
        window.localStorage.setItem('todos', JSON.stringify(todos))
    })

    return (
        <Paper style={{ padding: 0, margin: 0,display: 'inline-block', width:'100vw', height: '100vh', color:'inherit', background: 'inherit'}} elevation={0}>

            <Appbar color='primary' position='static' style={{height: '64px', minWidth:'720px', width: 'auto', overflow:'hidden'}} >
                <Toolbar>
                    <Typography >TODOS WITH HOOKS</Typography>
                </Toolbar>
                
            </Appbar>
            <Grid container justify='center' style={{marginTop: '1rem', minWidth: 720}} >
                <Grid item xs={10} md={7} lg={7} overflow='auto'>
            <TodoForm addTodo={addTodo}/>
            <TodoList todos={todos} removeTodo={removeTodo} toggleTodo={toggleTodo} editTodo={editTodo} changePriority={changePriority}/>
            </Grid>
            </Grid>
        </Paper>

    );
}

export default TodoApp;
